import React from "react";
import { useTranslation, Trans } from "react-i18next";
import { Container, Dropdown } from "semantic-ui-react";
import Social from "./Social";

export const langOptions = [
  { key: "en", value: "en", text: "En" },
  //{ key: "ru", value: "ru", text: "Ru" },
];

const Header: React.FC = () => {
  const { i18n, t } = useTranslation("layout");

  const changeLanguage = (e: any, se: any) => {
    i18n.changeLanguage(se.value);
  };

  return (
    <Container>
      <div className="menu-header space-bt">
        <div className="text-left">
          <div className="musk">“Musk’s Children”</div>
          <div className="community">
            {t("Community")}
            <span className="from">
              <Trans t={t} i18nKey="From">
                1<b>2</b>
              </Trans>
            </span>
          </div>
        </div>
        <div>
          <div className="space-bt lang-switch">
            <Dropdown
              name="lang"
              id="lang"
              options={langOptions}
              value={i18n.language}
              onChange={changeLanguage}
            />
            <div className="view-desktop social">
              <Social />
            </div>
          </div>
        </div>
      </div>
    </Container>
  );
};

export default Header;
